*\n * Figures\n */\nfigure > img {\n  display: block;\n}\n\nfigcaption {\n  font-size: $small-font-size;\n}\n\n/**\n * Lists\n */\nul,\nol {\n  margin-left: $spacing-unit;\n}\n\nli {\n  > ul,\n  > ol {\n    margin-bottom: 0;\n  }\n}\n\n/**\n * Headings\n */\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-weight: $base-font-weight;\n}\n\n/**\n * Links\n */\na {\n  color: $brand-color;\n  text-decoration: none;\n\n  &:visited {\n    color: color.adjust($brand-color, $lightness: -15%);\n  }\n\n  &:hover {\n    color: $text-color;\n    text-decoration: underline;\n  }\n}\n\n/**\n * Blockquotes\n */\nblockquote {\n  color: $grey-color;\n  border-left: 4px solid $grey-color-light;\n  padding-left: calc($spacing-unit / 2);\n  font-size: 18px;\n  letter-spacing: -1px;\n  font-style: italic;\n\n  > :last-child {\n    margin-bottom: 0;\n  }\n}\n\n/**\n * Code formatting\n */\npre,\ncode {\n  font-size: 15px;\n  border: 1px solid $grey-color-light;\n  border-radius: 3px;\n  background-color: #eef;\n}\n\ncode {\n  padding: 1px 5px;\n}\n\n.bold {\n  font-weight: bold;\n}\n\npre {\n  padding: 8px 12px;\n  overflow-x: auto;\n\n  > code {\n    border: 0;\n    padding-right: 0;\n    padding-left: 0;\n  }\n}\n\n/**\n * Wrapper\n */\n.wrapper {\n  max-width: calc(#{$content-width} - (#{$spacing-unit} * 2));\n  margin-right: auto;\n  margin-left: auto;\n  padding-right: $spacing-unit;\n  padding-left: $spacing-unit;\n\n  @extend %clearfix;\n\n  @include media-query($on-laptop) {\n    max-width: calc(#{$content-width} - (#{$spacing-unit}));\n    padding-right: calc($spacing-unit / 2);\n    padding-left: calc($spacing-unit / 2);\n  }\n}\n\n/**\n * Clearfix\n */\n%clearfix {\n  &::after {\n    content: \"\";\n    display: table;\n    clear: both;\n  }\n}\n\n/**\n * Icons\n */\n.icon {\n  display: inline-block;\n  width: 32px;\n  height: 32px;\n  vertical-align: middle;\n}\n","@charset \"utf-8\";\n\n@use 'sass:color';\n\n$base-font-family: \"Roboto Slab\", serif;\n$base-font-size: 18px;\n$base-font-weight: 400;\n$small-font-size: $base-font-size * 0.875;\n$base-line-height: 1.5;\n$spacing-unit: 30px;\n$text-color: #111;\n$background-color: #fdfdfd;\n$brand-color: #2a7ae2;\n$grey-color: #828282;\n$grey-color-light: color.adjust($grey-color, $lightness: 40%);\n$grey-color-dark: color.adjust($grey-color, $lightness: -25%);\n$content-width: 1000px;\n$on-palm: 600px;\n$on-laptop: 900px;\n\n// Use media queries like this:\n// @include media-query($on-palm) {\n//     .wrapper {\n//         padding-right: calc($spacing-unit / 2);\n//         padding-left: calc($spacing-unit / 2);\n//     }\n// }\n@mixin media-query($device) {\n  @media screen and (max-width: $device) {\n    @content;\n  }\n}\n","/**\n* Site header\n*/\n.site-header {\n  border-bottom: 1px solid $grey-color-light;\n  min-height: 56px;\n\n  // Positioning context for the mobile navigation icon\n  position: relative;\n}\n\n.site-title {\n  font-size: 26px;\n  font-weight: 300;\n  line-height: 56px;\n  letter-spacing: -1px;\n  margin-bottom: 0;\n  float: left;\n\n  &,\n  &:visited {\n    color: $grey-color-dark;\n  }\n}\n\n.site-nav {\n  float: right;\n  line-height: 56px;\n\n  .menu-icon {\n    display: none;\n  }\n\n  .page-link {\n    color: $text-color;\n    line-height: $base-line-height;\n\n    // Gaps between nav items, but not on the last one\n    &:not(:last-child) {\n      margin-right: 20px;\n    }\n  }\n\n  @include media-query($on-palm) {\n    position: absolute;\n    top: 9px;\n    right: calc($spacing-unit / 2);\n    background-color: $background-color;\n    border: 1px solid $grey-color-light;\n    border-radius: 5px;\n    text-align: right;\n\n    .menu-icon {\n      display: block;\n      float: right;\n      width: 36px;\n      height: 26px;\n      line-height: 0;\n      padding-top: 10px;\n      text-align: center;\n\n      > svg {\n        width: 18px;\n        height: 15px;\n\n        path {\n          fill: $grey-color-dark;\n        }\n      }\n    }\n\n    .trigger {\n      clear: both;\n      display: none;\n    }\n\n    &:hover .trigger {\n      display: block;\n      padding-bottom: 5px;\n    }\n\n    .page-link {\n      display: block;\n      padding: 5px 10px;\n\n      &:not(:last-child) {\n        margin-right: 0;\n      }\n\n      margin-left: 20px;\n    }\n  }\n}\n\n/**\n* Site footer\n*/\n.site-footer {\n  border-top: 1px solid $grey-color-light;\n  padding: $spacing-unit 0;\n}\n\n.footer-heading {\n  font-size: 18px;\n  margin-bottom: calc($spacing-unit / 2);\n}\n\n.contact-list,\n.social-media-list {\n  list-style: none;\n  margin-left: 0;\n}\n\n.footer-col-wrapper {\n  font-size: 15px;\n  color: $grey-color;\n  margin-left: calc(-1 * $spacing-unit / 2);\n\n  @extend %clearfix;\n}\n\n.footer-col {\n  float: left;\n  margin-bottom: calc($spacing-unit / 2);\n  padding-left: calc($spacing-unit / 2);\n}\n\n.footer-col-1 {\n  width: calc(35% - (#{$spacing-unit} / 2));\n}\n\n.footer-col-2 {\n  width: calc(20% - (#{$spacing-unit} / 2));\n}\n\n.footer-col-3 {\n  width: calc(45% - (#{$spacing-unit} / 2));\n}\n\n@include media-query($on-laptop) {\n  .footer-col-1,\n  .footer-col-2 {\n    width: calc(50% - (#{$spacing-unit} / 2));\n  }\n\n  .footer-col-3 {\n    width: calc(100% - (#{$spacing-unit} / 2));\n  }\n}\n\n@include media-query($on-palm) {\n  .footer-col {\n    float: none;\n    width: calc(100% - (#{$spacing-unit} / 2));\n  }\n}\n\n/**\n* Page content\n*/\n.page-content {\n  padding: $spacing-unit 0;\n}\n\n.page-heading {\n  font-size: 20px;\n}\n\n.post-list,\n.publication-list {\n  > ul {\n    margin-left: 0;\n    list-style: none;\n\n    > li {\n      margin-bottom: $spacing-unit;\n    }\n  }\n}\n\n.post-meta,\n.publication-meta {\n  font-size: $small-font-size;\n  color: $grey-color;\n}\n\n.post-link {\n  display: block;\n  font-size: 22px;\n}\n\n/**\n* Posts\n*/\n.post-header {\n  margin-bottom: $spacing-unit;\n}\n\n.post-title {\n  font-size: 42px;\n  letter-spacing: -1px;\n  line-height: 1;\n\n  @include media-query($on-laptop) {\n    font-size: 36px;\n  }\n}\n\n.post-content {\n  margin-bottom: $spacing-unit;\n\n  h1 {\n    font-size: 36px;\n\n    @include media-query($on-laptop) {\n      font-size: 32px;\n    }\n  }\n\n  h2 {\n    font-size: 32px;\n\n    @include media-query($on-laptop) {\n      font-size: 28px;\n    }\n  }\n\n  h3 {\n    font-size: 26px;\n\n    @include media-query($on-laptop) {\n      font-size: 22px;\n    }\n  }\n\n  h4 {\n    font-size: 20px;\n\n    @include media-query($on-laptop) {\n      font-size: 18px;\n    }\n  }\n}\n","/**\n * Syntax highlighting styles\n */\n.highlight {\n  background: #fff;\n\n  @extend %vertical-rhythm;\n\n  .highlighter-rouge & {\n    background: #eef;\n  }\n\n  // Comment\n  .c {\n    color: #998;\n    font-style: italic;\n  }\n\n  // Error\n  .err {\n    color: #a61717;\n    background-color: #e3d2d2;\n  }\n\n  // Keyword\n  .k {\n    font-weight: bold;\n  }\n\n  // Operator\n  .o {\n    font-weight: bold;\n  }\n\n  // Comment.Multiline\n  .cm {\n    color: #998;\n    font-style: italic;\n  }\n\n  // Comment.Preproc\n  .cp {\n    color: #999;\n    font-weight: bold;\n  }\n\n  // Comment.Single\n  .c1 {\n    color: #998;\n    font-style: italic;\n  }\n\n  // Comment.Special\n  .cs {\n    color: #999;\n    font-weight: bold;\n    font-style: italic;\n  }\n\n  // Generic.Deleted\n  .gd {\n    color: #000;\n    background-color: #fdd;\n  }\n\n  // Generic.Deleted.Specific\n  .gd .x {\n    color: #000;\n    background-color: #faa;\n  }\n\n  // Generic.Emph\n  .ge {\n    font-style: italic;\n  }\n\n  // Generic.Error\n  .gr {\n    color: #a00;\n  }\n\n  // Generic.Heading\n  .gh {\n    color: #999;\n  }\n\n  // Generic.Inserted\n  .gi {\n    color: #000;\n    background-color: #dfd;\n  }\n\n  // Generic.Inserted.Specific\n  .gi .x {\n    color: #000;\n    background-color: #afa;\n  }\n\n  // Generic.Output\n  .go {\n    color: #888;\n  }\n\n  // Generic.Prompt\n  .gp {\n    color: #555;\n  }\n\n  // Generic.Strong\n  .gs {\n    font-weight: